@import url(https://fonts.googleapis.com/css2?family=Assistant&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  src: url(/static/media/kumb-sans.7473a8f7.ttf);
  font-family: "Kumbh Sans";
}

:root {
  --color-primary: #397f6e;
  --color-primary-transparent: rgba(57, 127, 110, 0.2);
  --color-text: #000000;
  --color-disabled: #d9d9d9;
  --color-lightgrey: #f6f7f9;
  --color-lightgreen: #ebf2f1;
  --color-white: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Assistant", sans-serif;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 400;
  font-family: 'Kumbh Sans', sans-serif;
}

.hover {
  transition: box-shadow 0.1s ease-in-out;
  border-radius: .5rem;
}

.hover:hover {
  box-shadow: 0 0 0 0.33rem rgba(57, 127, 110, 0.2);
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}
header {
  height: 5em;
  padding: 1rem;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav,ul {
  height: 100%;
}

ul {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

ul > li > a {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul > li a {
  font-size: 17px;
  color: var(--color-text);
  text-decoration: none;
  padding: 0.5em;
}

nav > div {
  display: flex;
  grid-gap: .5rem;
  gap: .5rem;
  align-items: center;
}

nav > div > div {
  display: flex;
  align-items: center;
  grid-gap: .5rem;
  gap: .5rem;
}

.hamburger {
  width: 25px;
  height: 2px;
  background-color: var(--color-primary);
  order: 2;
  position: relative;
  display: none;
}

.hamburger::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  top: -7px;
  background-color: inherit
}

.hamburger::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  top: 7px;
  background-color: inherit
}

@media (max-width: 60rem) {
  .hamburger {
    display: block;
  }
  nav > ul > li:not(:first-child),
  nav > div {
    display: none;
  }
}
.btn {
  padding: 12.75px 25.5px;
  border-radius: 3.4px;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  font-size: 17px;
  display: inline-flex;
  transition: all .1s ease-in-out;
}

.btn.small {
  padding: 0.5rem 1rem;
}

.btn.full {
  background-color: var(--color-primary);
  color: white;
}

.btn:hover {
  box-shadow: 0 0 0 0.33rem var(--color-primary-transparent);
}
article {
  height: 450px;
  width: 100%;
  background-color: #fff;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
}

article > img,
article > .article-body {
  width: 50%;
}

.article-body {
  padding: 1em 2em 1em 3em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
}

.article-body > p:first-child {
  color: #B5B5B5;
  font-size: 17px;
  margin-bottom: 2em;
}

.article-body > p {
  font-size: 21px;
  margin-bottom: 1em;
}

.article-body > h2 {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 1em;
}
.hero {
  width: 90%;
  height: 60vh;
  margin: 0 auto;
  background-color: var(--color-lightgreen);
  padding: 1rem;
  border-radius: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 8rem;
}

.hero > div {
  max-width: 600px;
  text-align: center;
}

.hero h1 {
  font-size: 58px;
  line-height: 1.15em;
  letter-spacing: -0.03em;
  margin-bottom: 1.5rem;
}

.hero h1 > span {
  color: var(--color-primary)
}

.hero p {
  font-size: 21px;
}

.home-grid {
  display: grid;
  padding: 5%;
  background-color: var(--color-lightgrey);
  grid-template-columns: auto 300px;
  grid-column-gap: 2rem;
  column-gap: 2rem; 
}

@media (max-width: 50rem) {
  .hero h1 {
    letter-spacing: -.03em;
    font-size: 2.5rem;
  }
}
